.box-primary {
  text-align: center;
}

.box-primary:before {
  content: '';
  display: block;
  width: 100%;
  padding-top: 100%;
  background-color: #4ACD8A;
  margin-bottom: 0.625em;
}

.box-primary:after {
  content: "#4ACD8A";
  display: block;
}

